.fade-in{
  -webkit-animation: fadeIn 2s ease-in-out 0s 1;
          animation: fadeIn 2s ease-in-out 0s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.fade-out{
  -webkit-animation: fadeOut 2s ease-in-out 0s 1;
          animation: fadeOut 2s ease-in-out 0s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
    0%{
      opacity: 100;
    }
    100%{
      opacity: 0;
    }
}

@keyframes fadeIn {
    0%{
      opacity: 100;
    }
    100%{
      opacity: 0;
    }
}

@-webkit-keyframes fadeOut {
    0%{
      opacity: 0;
    }
    100%{
      opacity: 100;
    }
}

@keyframes fadeOut {
    0%{
      opacity: 0;
    }
    100%{
      opacity: 100;
    }
}

.slide-out{
  -webkit-animation: slideOut 2s ease-in-out 0s 1;
          animation: slideOut 2s ease-in-out 0s 1;
  -webkit-animation-fill-mode: forwards;
