}

.rotating-clock{
  -webkit-animation: rotatingClockwise 2s ease-in-out 0s 1;
          animation: rotatingClockwise 2s ease-in-out 0s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.rotating-counter-clock{
  -webkit-animation: rotatingCounterClockwise 2s ease-in-out 0s 1;
          animation: rotatingCounterClockwise 2s ease-in-out 0s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes rotatingClockwise {
  0%{
    opacity: 100;
  }
  100%{
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
    opacity:0;
  }
}

@keyframes rotatingClockwise {
  0%{
    opacity: 100;
  }
  100%{
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
    opacity:0;
  }
}

@-webkit-keyframes rotatingCounterClockwise {
  0%{
    opacity: 0;
  }
  100%{
    -webkit-transform: rotate(-720deg);
            transform: rotate(-720deg);
    opacity: 100;
  }
}

@keyframes rotatingCounterClockwise {
  0%{
    opacity: 0;
  }
  100%{
    -webkit-transform: rotate(-720deg);
            transform: rotate(-720deg);
    opacity: 100;
  }
}

body{
  background: -webkit-linear-gradient(135deg, #e2c48b, 	#f6ecc8, #e7cac9, #f3bfb2);
  background: linear-gradient(-45deg, #e2c48b, 	#f6ecc8, #e7cac9, #f3bfb2);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
          animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}


